* {
    margin: 0px;
    padding: 0px;
}

html, body {
    color: #444446;
    font-size: 62.5%;
    font-family:  'Noto Sans JP', '游ゴシック体', 'Yu Gothic', YuGothic, sans-serif;
    font-style: normal;
    font-feature-settings: 'palt';
    box-sizing: border-box;
    background-color: #fff;
}



/* 全要素の余白 */
.container {
    max-width: 1300px;
    padding: 0 40px;
    margin: 0 auto;
}



/* ヘッダ */
.header {
    background-color: black;
    position: fixed;
    width: 100%;
}

.header > .container {
    height: 80px;
    display: flex;
    align-items: center;
}

.area_logo_header .logo {
    display: block;
}

.area_logo_header .logo img {
    height: 30px;
    vertical-align: bottom;
}



/* ナビゲーション */
.area_nav_header {
    margin-left: auto;
}



/* リストナビ */
.list_nav_header {
    list-style: none;
    display: flex;
    align-items: center;
}

.list_nav_header a {
    margin-left: 30px;
    text-decoration: none;
    color: white;
    font: 600 15px/1em '游ゴシック体', 'Yu Gothic', YuGothic, sans-serif ;
}

.list_nav_header  .btn_contact {
    color: black;
    display: flex;
    align-items: center;
    height: 36px;
    padding: 0 24px;
    background: rgb(245, 197, 30);
    border-radius: 2px;
}



/* ボディ 背景色*/
.area_home_main , .area_home_about , .area_home_merit , .area_home_feature , .area_home_cost, .area_home_step{
    padding: 90px 0;
    width: 100%;
}

.area_home_main , .area_home_merit , .area_home_step{
    background:rgb(245, 197, 30);
}
.area_home_about {
    background: linear-gradient(180deg, #efefeb 20%, #fff 90%);
}

.area_home_cost {
    background:blanchedalmond;
}



/* ボディ　タイトル　共通項目 */
.head_home {
    text-align: center;
    margin-bottom: 80px;
    display: flex;
    justify-content: center;
    align-items: center;
    /* position: relative; */
}

.ttl_eng {
    font-size: 12rem;
    color: #fff5dd;
    font-weight: 300;
    line-height: 1;
    letter-spacing: 0px;
    text-transform: uppercase;
    font-weight: 350;
}

.ttl_ja {
    font-size: 2.4rem;
    font-weight: bold;
    letter-spacing: 0.24em;
    padding: 30px 0 0 30px;
    /* justify-content: center; */
}

.body_contents {    
    display: flex;
    justify-content: center;
    vertical-align: baseline;
}

.body_goods {
    margin-right: 120px;
}

/* ヘッダ固定した分の余白下げ */
.body_goods, .main_image{
    margin-top: 80px;
}



/* ボディ MAIN*/
.btn_design_gallery {
    display: flex;
    justify-content: center;
    text-decoration: none;
    text-align: center;
    color: rgb(245, 197, 30);
    width: 70%;
    margin: 30px  auto;
    padding:  20px 50px ;
    background: black;
    border-radius: 10px;
    font: 600 22px/1em '游ゴシック体', 'Yu Gothic', YuGothic, sans-serif ;
}

.btn_design_gallery:hover , .btn_contact_link:hover {
    background-color:white;
    color: black;
}



/* ボディ　ABOUT */
.body_about_txt {
    font-size: 5.5rem;
    line-height: 1.5em;
    font-weight: 500;
    margin-bottom: 30px;
    letter-spacing: 5px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
}

.body_about_smalltxt {
    font-size: 2.5rem;
    line-height: 2em;
    font-weight: bold;
    letter-spacing: 5px;
    display: flex;
    flex-wrap: wrap;
    flex-direction: column;
}



/* ボディ FEATURE */
.body_feature{
    margin: 150px auto;
    display: flex;
    justify-content: space-around;
}

.feature_ttl{
    font-size: 60px;
}

.feature_contents{
    /* 縦に中央揃え */
    display:flex;
    justify-content: center;
    flex-direction:column;
}

.feature_contents_center{
    text-align: center;
}

.body_feature_txt, .feature_txt{
    /* font-size: 2rem; */
    font-size: 25px;
    line-height: 1.5em;
    font-weight: bold;
    display: flex;
    justify-content: flex-start;
    flex-direction: column;
}

.feature_txt{
    font-size: 16px;
    margin-top: 20px;
}

.push_color, .stamp_color, .coupon_color, .point_color, .photo_color{
    border-radius: 70px ;
    margin: 5px 0;
    padding: 6px 0;
    font-size: 15px;
    font-weight: bold;

}

/* 小見出し背景色 */
.push_color, .point_color{
    background-color: #f5d61e;
    color: #000;
}

.point_color{
    text-align: center;
}

.stamp_color, .photo_color{
    background-color: #0000ff;
    color:#ffffff;
    text-align: center;
}

.coupon_color{
    background-color: #c30d23;
    color:#ffffff;
    text-align: center;
}



/* ボディ　COST */
.body_cost{
    display: flex;
    justify-content: space-around;
    padding-bottom: 50px;

}

.cost_ttl{
    font-size: 45px;
    line-height: 80px;
    padding-bottom: 20px;
    
}

.cost_ttl > b{
    font-size: 80px;
    vertical-align: -5px;
    font-family: Impact, Haettenschweiler, 'Arial Narrow Bold', sans-serif;
}

.body_cost_ttl > span{
    font-size: 2.5rem;
    font-weight: bold;
}

.pricebox{
    background-color: rgb(245, 197, 30);
    width: 250px;
    height: 250px;
    display: table-cell;
    vertical-align: middle;
    text-align: center;
}
.price2 {
    font-size: 20px;

}

.pricetxt{
    font-size: 25px;
    font-weight: bold;
}

.price{
    display: block;
    font-size: 30px;
    font-weight: bold;
}

.price > b {
    font-size: 70px;
    letter-spacing: -0.2rem;
}

.menu_image{
    display: flex;
    justify-content: center;
    width: 100%;
}
    

    
/* ボディ　STEP */
.btn_contact_link{
    display: flex;
    justify-content: space-around;
    text-decoration: none;
    text-align: center;
    color: rgb(245, 197, 30);
    margin: 80px auto 0;
    padding:  20px 50px ;
    width: 30%;
    background: black;
    border-radius: 10px;
    font: 600 22px/1em '游ゴシック体', 'Yu Gothic', YuGothic, sans-serif ;
}
    
.step_contents{
    padding: 20px;
}

.step_contents > h2 {
    font-size: 25px;
    padding-top: 20px;
    text-align: center;
}


/* フッタ */
footer{
    background: rgb(245, 197, 30);
}

footer > .container{
    padding: 0 0 10px;
    display: flex;
    flex-direction: column;
    align-items: center;
}

.list_sns{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    margin: 0 -10px;
}

.list_sns > li {
    padding: 50px 10px 10px;
    list-style: none;
}

.list_sns a {
    display: block;
    width: 40px;
    height: 40px;
    background: #000;
    border-radius: 50%;
    position: relative;
}

.list_sns a:before{
    content: "";
    display: block;
    width: 30px;
    height: 30px;
    background-size: contain;
    background-repeat: no-repeat;
    background-position: center center;
    position: absolute;
    top: 50%;
    left: 50%;
    transform: translate( -50% , -50% );
}

.list_sns .instagram a:before{
    background: url(img/ico/ico_sns_instagram.svg);
}
.list_sns .facebook a:before{
    background: url(img/ico/ico_sns_facebook.svg);
}

.copyright{
    font: 300 14px/1rem 'furura PT' , 'noto sans japanise' , sans-serif;
    margin: 30px 0 30px;
}




/* レスポンシブ */
@media screen and (max-width: 480px) {



    .body_contents, .body_feature {
        display: block;
    }

    .body_about_txt {
        font-size: 10px;
    }
    
    .body_about_smalltxt {
        font-size: 5px;
    }


    .step_contents > h2 {
        font-size: 5px;

    }
    
    .feature_contents > img {
        width: auto;
        height: auto;
    }



}